  border: 2px solid #8f00ff;
  box-sizing: border-box;
  border-radius: 10px;
  background: transparent;
  font-family: right;
  color: #8f00ff;
  width: 20%;
  --c: #8f00ff;
  color: var(--c);
  font-size: 16px;
  border: 0.1em solid var(--c);
  border-radius: 10px 40px;
  width: 12em;
  height: 3em;
  /* text-transform: uppercase; */
  font-weight: bold;
  letter-spacing: 0.1em;
  text-align: center;
  line-height: 3em;
  position: relative;
  overflow: hidden;
  z-index: 1;
  transition: 0.5s;
  margin: 1em;
}
.learnbtn:hover {
  color: #fff;
}
.learnbtn span {
  position: absolute;
  width: 25%;
  height: 100%;
  background-color: var(--c);
  transform: translateY(150%);
  border-radius: 50%;
  left: calc((var(--n) - 1) * 25%);
  transition: 0.6s;
  transition-delay: calc((var(--n) - 1) * 0.1s);
  z-index: -1;
}
.learnbtn:hover span {
  transform: translateY(0) scale(2);
}

.learnbtn span:nth-child(1) {
  --n: 1;
}
.learnbtn span:nth-child(2) {
  --n: 2;
}

.learnbtn span:nth-child(3) {
  --n: 3;
}

.learnbtn span:nth-child(4) {
  --n: 4;
}

